export default {
	data() {
		return {
			is_pay:true,
			code: '',
		    tel: '',
		    show_code: false, // 二维码弹窗
		    show_letter: false, // 展示信
		    show: false,
		    nickname: '',
			animationData: {}, //自定义动画
		}
	},
	onShow() {
		this.$api.MyselfIndex({
			token: uni.getStorageSync("token"),
			user_id: uni.getStorageSync("userId"),
			
		}).then(res=>{
			console.log(res)
			this.nickname = res.data.data.nickname
		})
		// 动态创建遮罩特效
		var animation = uni.createAnimation({
			  duration: 300,
			  timingFunction: 'linear',
			})			
			this.animation = animation			
			animation.translateY(-500).step()			
			this.animationData = animation.export()
			setTimeout(function() {
			  animation.translateY(0).step()
			  this.animationData = animation.export()
			}.bind(this), 300)
	},
	onLoad() {
		this.getData()
	},
	methods: {
		start(){
			this.is_pay = false
		},
		address(){
			uni.navigateTo({
				url:'../../pagesMy/PayStep/PayStep'
			})
		},
		pic(){
			uni.navigateTo({
				url:'../../pagesMy/view/view?url='+ "https://web.detion.com/VipDetail"
			})
		},
		copy(){
			uni.setClipboardData({
			    data:String(this.tel),
			    success: function (res) {
			        uni.getClipboardData({
						success: function(res) {
							// console.log(res)
							uni.showToast({
								title: '已复制到剪贴板'
							});
						}
					});
			    }
			});
		},
		// 预览二维码
		pre(){
			uni.previewImage({
				urls: [this.code],
				longPressActions: {
					itemList: ['发送给朋友', '保存图片', '收藏'],
					success: function(data) {
						// console.log('选中了第' + (data.tapIndex + 1) + '个按钮,第' + (data.index + 1) + '张图片');
					},
					fail: function(err) {
						// console.log(err.errMsg);
					}
				}
			});
		},
		// 获取二维码
		getData(){
			this.$api.service_er_mobile({
				
			}).then(res=>{
				this.tel = res.data.data.mobile
				this.code = res.data.data.er
			})
		}
	}
}
